<template>
  <div class="pdf-container">
    <CustomSidebarButton :targetSelector="targetSelector" v-for="targetSelector in customButtons" :key="targetSelector" />
  </div>
</template>

<script>
import PSPDFKit from "pspdfkit";
import CustomSidebarButton from "@/components/CustomSidebarButton";

export default {
  name: 'PSPDFKit',
  components: {
    CustomSidebarButton,
  },
  /**
	 * The component receives `pdfFile` prop which is type of String and required
	 */
  props: {
    pdfFile: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      customButtons: this.targetSelectors
    }
  },
  /**
	* We wait until the template has been rendered to load the document into the library.
	*/
  mounted() {
    this.loadPSPDFKit().then((instance) => {
      this.$emit("loaded", instance);
    });
  },
  /**
	 * We watch for `pdfFile` prop changes and trigger unloading and loading when there's a new document to load.
	 */
  watch: {
    pdfFile(val) {
      if (val) {
        this.loadPSPDFKit();
      }
    },
  },
  /**
	 * Our component has `loadPSPDFKit` method, this unloads and cleanup the component and triggers document loading.
	 */
  methods: {
    async loadPSPDFKit() {
      PSPDFKit.unload(".pdf-container");
      return PSPDFKit.load({
        // access the pdfFile from props
        document: this.pdfFile,
        container: ".pdf-container",
        customUI: {
          [PSPDFKit.UIElement.Sidebar]: {
            [PSPDFKit.SidebarMode.ANNOTATIONS]({containerNode}) {
              containerNode.style.padding = '0.5rem';
              return {
                node: containerNode,
                onRenderItem({itemContainerNode, item: annotation}) {
                  const footer = itemContainerNode.querySelector('.PSPDFKit-Sidebar-Annotations-Footer');

                  if (!itemContainerNode.querySelector('.noteContainer')) {
                      const noteSpan = document.createElement('div');
                      const targetSelector = 'noteContainer-'+Math.random().toString(36).substring(7)
                      this.targetSelectors.push(`.${targetSelector}`)
                      noteSpan.classList.add(targetSelector);
                      if (annotation.note) {
                          noteSpan.textContent = annotation.note
                      }
                      noteSpan.style.color = '#293745';
                      noteSpan.style.fontSize = '14px';
                      footer.prepend(noteSpan);
                  }

                  const annotationIcon = itemContainerNode.querySelector('.PSPDFKit-Icon-Highlight');
                  if (annotationIcon && annotation.color)
                      annotationIcon.style.color = annotation.color.toHex();
                }
              };
            }
          }
        }
      });
    },
  },

  /**
	 * Clean up when the component is unmounted; so, it is ready to load another document.
	 */
  beforeUnmount() {
    PSPDFKit.unload(".pdf-container");
  },
};
</script>


<style scoped>
.pdf-container {
  height: 100vh;
}
</style>
